@media (max-width: 1440px) {
    .logo_part {
        padding-bottom: 120px; }

    .menu_burger {
        position: static;
        margin-right: 45px; }

    .header_text_container {
        padding-left: 80px; }

    .scroll_down {
        left: 0; }

    .header_img {
        height: 800px;
        width: auto;
        right: -130px; } }

@media (max-width: 1200px) {
    .phone_number {
        font-size: 16px;
        transform: translateY(5px); }
    .phone .feedback {
        font-size: 10px;
        transform: translateY(-5px); }
    .header_text_header {
        font-size: 36px; }
    .header_return {
        font-size: 24px; }
    .header_text_container .feedback_btn {
        margin-bottom: 150px; }

    .section_header {
        font-size: 28px !important; }

    #about_us {
        font-size: 14px; }


    .feature_header {
        font-size: 17px; }
    .feature_description {
        font-size: 13px; }
    .feature_img_container {
        transform: scale(.8);
        margin-right: 20px; }
    .feature {
        padding: 0 10px;
        margin-bottom: 40px; }


    .capture_text {
        font-size: 28px; }
    .capture_container .feedback_btn {
        padding: 25px 80px;
        font-size: 14px; }


    #services {
        min-height: 900px;
        padding-top: 60px;
        .section_header {
            padding-bottom: 90px; } }
    .services_img {
        transform: translate(-55%, 2%) scale(.85); }
    .services_ellipse {
        transform: translateX(-50%) scale(.85); }
    .servisces_list {
        transform: translate(-50%, 40%) scale(.85); }


    #catalog {}
    padding-top: 45px;
    padding-bottom: 30px;
    .section_header {
        margin-bottom: 35px; }
    .catalog_item_header {
        font-size: 20px;
        margin-bottom: 15px;
        margin-top: 25px; }
    .catalog_item_description {
        font-size: 14px; }
    .catalog_item_btn {
        font-size: 12px;
        padding: 20px 40px; }
    .price {
        font-size: 28px;
        span {
            font-size: 18px; } }


    .review {
        padding: 30px; }
    .review_next {
        right: 10%; }
    .review_prev {
        left: 10%; }



    #modal_feedback .iziModal-content {
        padding: 40px 60px !important; }
    .modal_form {
        input {
            font-size: 16px; }
        ::placeholder {
            font-size: 16px; } }

    #privacy_modal {
        max-width: calc(100% - 40px) !important; }
    #privacy_modal .iziModal-content {
        padding: 17px 20px 30px !important; }
    #close_privacy_modal {
        top: 17px;
        right: 20px; } }

@media (max-width: 980px) {
    .contacts {
        display: none; }
    .logo {
        position: static; }
    .logo_part {
        position: relative;
        padding-bottom: 85px; }
    .menu_burger {
        position: absolute;
        right: 15px;
        margin: 0;
        left: auto;
        transform: scale(-1, 1); }


    .header_text_header {
        font-size: 28px;
        line-height: 35px; }
    .header_return {
        font-size: 18px;
        line-height: 22px;
        margin-bottom: 50px; }
    .header_feature_text {
        font-size: 12px; }
    .scroll_down {
        top: 210px; }
    .header_text_container .feedback_btn {
        margin-bottom: 100px;
        margin-top: 60px;
        padding: 24px 60px; }
    .header_img {
        height: 550px;
        right: -90px;
        bottom: -60px; }

    #modal_menu {
        margin-top: auto;
        .iziModal-content {
            padding-bottom: 35px !important; }
        .contacts {
            display: block; }
        .phone {
            margin-right: 0;
            text-align: center;
            margin-bottom: 35px; }
        .phone_number {
            font-size: 24px;
            color: #000;
            transform: none;
            margin: 70px 0 5px; }
        .feedback {
            transform: none;
            font-size: 16px; } }

    #about_us {
        margin-bottom: 80px;
        margin-top: 70px; }
    .about_container {
        flex-wrap: wrap-reverse; }
    .about_content {
        flex-basis: 100%; }
    .about_us_img {
        flex-basis: 100%;
        margin: 0 0 50px;
        height: 500px; }

    .capture {
        padding: 80px 0; }

    .capture_container {
        flex-wrap: wrap;
        .feedback_btn {
            padding: 25px 100px;
            margin-top: 50px; } }

    #services {
        min-height: 790px;
        padding-top: 50px;
        .section_header {
            padding-bottom: 55px; } }
    .services_img {
        transform: translate(-55%, 2%) scale(.675); }
    .services_ellipse {
        transform: translateX(-50%) scale(.675); }
    .servisces_list {
        transform: translate(-50%, 83%) scale(.675); }

    .catalog_item_btn {
        padding: 20px 60px; }

    .review_next {
        right: 0; }
    .review_prev {
        left: 0; }


    .footer_feedback_form {
        padding: 30px 30px;
        input {
            max-width: 49%; } }

    .col {
        width: 50%; } }

@media (max-width: 767px) {
    .logo_wrap {
        flex-direction: column; }
    .logo_text {
        border: none;
        padding: 0;
        margin-bottom: 3px; }
    .logo_description {
        padding: 0; }

    .scroll_down {
        display: none; }

    .header_text_container {
        padding: 0; }

    .pre_header {
        font-size: 12px; }
    .header_text_header {
        font-size: 20px; }
    .header_return {
        font-size: 14px;
        margin-top: 30px;
        margin-bottom: 35px; }
    .header_features {
        flex-wrap: wrap; }
    .header_feature {
        width: 100%;
        margin: 10px 0; }
    .menu {
        text-align: center; }
    .header_img {
        height: 430px;
        right: -65px;
        bottom: -50px; }
    .header_text_container .feedback_btn {
        width: 100%; }

    .about_us_img {
        height: 340px; }

    .section_header {
        font-size: 20px !important;
        line-height: 25px !important; }

    #about_us {
        margin-top: 50px;
        margin-bottom: 40px;
        padding: 40px 15px; }
    #about_us .section_header {
        margin-bottom: 40px; }

    #features .section_header {
        max-width: 340px;
        margin-bottom: 50px; }
    .feature_header {
        font-size: 20px;
        margin-bottom: 4px; }
    .feature_description {
        font-size: 16px; }

    .capture_container .feedback_btn {
        width: 100%; }

    #services {
        min-height: auto;
        padding: 50px 15px;
        .section_header {
            padding: 0;
            margin-bottom: 50px; } }
    .servisces_list {
        position: static;
        transform: none;
        height: auto;
        min-width: auto;
        li {
            position: static;
            transform: none;
            margin: 20px 0;
            align-items: flex-start;
            span {
                display: inline;
                font-size: 18px;
                position: static;
                transform: none;
                margin-left: 10px; } }
        li:nth-child(n+8) span {
            transform: none;
            text-align: left; } }

    .services_ellipse,
    .services_img {
        display: none; }

    #catalog {
        padding: 40px 0;
        .section_header {
            margin-bottom: 30px; } }
    .price {
        font-size: 30px;
        span {
            font-size: 16px; } }
    .catalog_item {
        margin-bottom: 50px; }

    #reviews {
        padding-top: 40px;
        .section_header {
            text-align: left; }
        .feedback_btn {
            width: 100%; } }
    .slider {
        margin-top: 40px;
        margin-bottom: 50px; }
    .owl-nav {
        display: none !important; }
    .footer_feedback_text {
        font-size: 14px; }
    .footer_feedback_form {
        flex-wrap: wrap;
        input {
            width: 100%;
            max-width: 100%; }
        input[type="submit"] {
            margin-top: 30px; } }
    .footer_feedback_img {
        width: 375px;
        height: auto; }
    footer .privacy {
        display: none; }

    .modal {
        max-width: calc(100% - 40px) !important;
        max-height: calc(100% - 40px); }

    #modal_feedback {
        .iziModal-content {
            padding: 35px 20px 45px !important; }
        input {
            font-size: 16px;
            padding: 15px 20px; }
        ::placeholder {
            font-size: 16px; }
        .feedback_btn {
            font-size: 14px;
            padding: 22px 0; } }
    .modal_feedback_header {
        font-size: 20px;
        margin-bottom: 30px; }
    #close_feedback_modal {
        top: 22px;
        right: 22px; }

    #modal_thank .iziModal-content {
        padding: 50px 40px 40px !important; }
    .modal_thank_header {
        font-size: 20px; }
    .modal_thank_text {
        font-size: 14px; }
    .modal_thank_img {
        margin-bottom: 15px; }
    #close_thank_modal {
        top: 22px;
        right: 22px; }
    #close_menu_modal {
        top: 30px;
        right: 30px; }

    #close_privacy_modal {
        top: 20px;
        right: 14px; }

    .modal_close_btn {
        transform: scale(.7); }

    .col {
        width: 100%; } }

@media (max-width: 375px) {
    .logo_description {
        font-size: 10px; }
    .header_text_container .feedback_btn,
    .capture_container .feedback_btn,
    #reviews .feedback_btn,
    .footer_feedback_form input[type="submit"] {
        padding: 22px 0; }
    .catalog_item_btn {
        padding: 20px 50px; }
    .footer_feedback_form input[type="submit"] {
        font-size: 12px; }
    .footer_feedback_form input[type="text"],
    .footer_feedback_form input[type="tel"] {
        font-size: 14px; }
    .footer_feedback {
        ::placeholder {
            font-size: 14px; } } }

@media (max-height: 632px) {
    #modal_menu {
        margin-top: 0 !important;
        border-top-right-radius: 0 !important;
        border-bottom-right-radius: 0 !important;
        min-height: 100% !important; } }
@media (max-width: 980px) and (max-height: 850px) {
    #modal_menu {
        margin-top: 0 !important;
        border-top-right-radius: 0 !important;
        border-bottom-right-radius: 0 !important;
        min-height: 100% !important; } }
