footer {
    .logo_part {
        margin-top: 50px;
        margin-bottom: 35px;
        padding: 0;
        margin: 35px 0; }
    .phone_number {
        color: #131313;
        &:hover {
            color: #131313; } }
    .messanger:hover {
        filter: brightness(75%); }
    .phone .feedback {
        transition: all .3s;
        &:hover {
            transition: all .3s;
            border-color: darken(#FA4343, 20%);
            color: darken(#FA4343, 20%); } }

    .logo_text {
        border-color: #878787; }
    hr {
        background-color: #E9E9E9;
        border: none;
        height: 1px;
        margin: 0; } }

.copyright_container {
    padding-top: 16px;
    padding-bottom: 16px;
    font-size: 11px;
    color: #878787;
    display: flex;
    justify-content: space-between; }

.privacy {
    text-decoration-line: underline;
    cursor: pointer;
    font-size: 11px;
    color: #878787;
    transition: all .3s;
    &:hover {
        transition: all .3s;
        color: darken(#878787, 20%); } }
