#reviews {
    background: #171717;
    padding-top: 70px;
    padding-bottom: 110px;
    background-repeat: no-repeat;
    background-position: center top;
    overflow-x: hidden;
    .section_header {
        font-size: 36px;
        line-height: 44px;
        color: #FFFFFF;
        text-align: center;
        margin-bottom: 0; }
    .feedback_btn {
        margin: 0 auto;
        display: block;
        z-index: 30;
        position: relative; } }

.slider {
    position: relative;
    margin-bottom: 60px;
    margin-top: 70px; }

.review_container {
    display: flex;
    justify-content: center; }

.review {
    width: 570px;
    min-height: 340px;
    padding: 30px 40px;
    background-color: #fff;
    border-radius: 10px; }

.review_header_container {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: 20px; }

.review_img {
    width: 80px;
    height: 80px;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 50%;
    margin-right: 25px; }

.review_name {
    font-family: 'Averta CY Bold';
    font-size: 20px;
    line-height: 25px;
    color: #131313; }

.review_instagram {
    display: flex;
    align-items: center;
    font-size: 14px;
    line-height: 17px;
    color: #131313; }

.ints_icon {
    width: 20px !important;
    height: 20px;
    margin-right: 8px; }

.review_text {
    font-size: 14px;
    line-height: 17px;
    color: #000000;
    margin-bottom: 0; }

.owl-stage-outer {
    overflow: visible !important; }

.owl-item {
    transform: scale(.75) !important;
    filter: blur(2px);
    transition: all 1s; }

.owl-item.center {
    transform: none !important;
    filter: blur(0); }

.owl-nav {
    position: absolute;
    top: 50%;
    width: 100%;
    height: 60px;
    transform: translateY(-50%); }

.owl-next:focus,
.owl-prev:focus {
    outline: 0; }


.review_prev,
.review_next {
    padding: 20px;
    background-color: #fff;
    border-radius: 50%;
    position: absolute;
    top: 0;
    right: 15.7%;
    transition: all .3s;
    &:hover {
        transition: all .3s;
        background-color: darken(#fff, 10%); } }

.review_prev {
    left: 15.7%;
    transform: rotate(180deg); }
