.footer_feedback {
    padding-top: 75px;
    background: #171717;
    font-family: 'Averta CY Regular';
    color: #FFFFFF;
    span {
        font-family: 'Averta CY Bold'; }
    .container {
        position: relative;
        padding-bottom: 120px; } }


.footer_feedback_container {
    max-width: 900px;
    z-index: 10;
    position: relative; }

.footer_feedback_header {
    font-size: 36px;
    line-height: 44px;
    margin-bottom: 40px; }

.footer_feedback_text {
    font-size: 18px;
    line-height: 22px;
    margin-bottom: 35px; }

.footer_feedback_form {
    position: relative;
    background: #FFFFFF;
    border-radius: 10px;
    padding: 30px 35px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &:after {
        content: '';
        position: absolute;
        width: 95%;
        height: 15px;
        background: #252525;
        border-bottom-right-radius: 10px;
        border-bottom-left-radius: 10px;
        bottom: -15px;
        left: 50%;
        transform: translateX(-50%); } }

.footer_feedback_img {
    position: relative;
    z-index: 1; }


.footer_images {
    position: absolute;
    right: 0;
    bottom: 40px; }

.footer_feedback_bg {
    position: absolute;
    right: 0;
    bottom: -40px; }
