#about_us {
    margin-top: 100px;
    margin-bottom: 130px;
    font-family: 'Averta CY Regular';
    font-size: 18px;
    line-height: 26px;
    text-indent: 10px;
    color: #131313;
    .section_header {
        margin-bottom: 55px; } }

.about_container {
    display: flex;
    justify-content: space-between;
    align-content: stretch; }

.about_us_img {
    box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    background-size: cover;
    background-position: top left;
    background-repeat: no-repeat;
    flex-basis: 50%;
    margin-left: 30px; }
.about_content {
    flex-basis: 50%; }

.about_us_text {
    margin-bottom: 40px; }

.models_header {
    font-family: 'Averta CY Bold';
    margin-bottom: .5rem; }

.models li:before {
    content: '- ';
    padding-left: 10px; }
