#services {
    background-color: #171717;
    background-repeat: no-repeat;
    background-position: center 0;
    padding-top: 75px;
    min-height: 1075px;
    .section_header {
        color: #fff;
        padding-bottom: 170px; } }

.services_container {
    margin: 0 auto;
    position: relative;
    font-family: 'Averta CY SemiBold';
    font-size: 18px;
    color: #FFFFFF;
    span {
        position: relative;
        display: inline-block;
        left: 20px; } }

.services_ellipse {
    position: absolute;
    left: 50%;
    transform: translateX(-50%); }

.servisces_list {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    min-width: 1168px;
    height: 100px;
    z-index: 10; }

.servisces_list li {
    display: flex;
    align-items: center;
    position: absolute;
    z-index: 10; }

.servisces_list li:nth-child(n+8) span {
    left: auto;
    text-align: right;
    transform: translateX(calc(-100% - 50px)); }


.service_feddback_btn {
    padding: 15px;
    border-radius: 50%;
    background: #FA4343;
    box-shadow: 0px 4px 20px rgba(250, 67, 67, 0.2);
    border: none;
    background-position: center center;
    background-repeat: no-repeat;
    transition: all .3s;
    &:hover {
        transition: all .3s;
        box-shadow: 0px 15px 25px rgba(250, 67, 67, 0.3);
        background-color: darken(#FA4343, 3%); }
    &:focus {
        outline: 0; } }

