@import "../../node_modules/bootstrap/scss/bootstrap-reboot.scss";
@import "../fonts/stylesheet.css";

ymaps {
    position: relative;
    z-index: 0; }

* {
    box-sizing: border-box; }

body {
    font-family: 'Averta CY Regular';
    position: relative;
    width: 100%;
    max-width: 100%;
    overflow-x: hidden; }
html {
    width: 100%;
    max-width: 100%;
    overflow-x: hidden; }

.container {
    max-width: 1200px;
    width: 100%;
    padding: 0 15px;
    margin: 0 auto; }

.row {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 0 -15px; }

.col {
    width: 33.3333%;
    padding: 0 15px; }

ul {
    margin: 0;
    padding: 0;
    list-style: none; }

.section_header {
    font-family: 'Averta CY Bold';
    font-size: 36px;
    line-height: 44px;
    color: #131313; }

.feedback_btn {
    font-family: 'Averta CY Bold';
    font-size: 14px;
    line-height: 17px;
    color: #FFFFFF;
    background: #FA4343;
    box-shadow: 0px 15px 25px rgba(250, 67, 67, 0.2);
    border-radius: 50px;
    border: none;
    padding: 25px 95px;
    transition: all .3s;
    cursor: pointer;
    &:hover {
        transition: all .3s;
        box-shadow: 0px 15px 25px rgba(250, 67, 67, 0.3);
        background: darken(#FA4343, 3%); }
    &:focus {
        outline: 0; } }

input[type="text"],
input[type="tel"] {
    background: #F0F0F0;
    box-shadow: inset 0px 4px 20px rgba(0, 0, 0, 0.05);
    border-radius: 60px;
    border: none;
    width: 440px;
    height: 60px;
    padding: 19px 30px;
    font-size: 18px;
    line-height: 22px;
    color: #131313;
    &:focus {
        outline: 0; } }

input[type="submit"] {
    background: #FA4343;
    box-shadow: 0px 15px 25px rgba(250, 67, 67, 0.2);
    border-radius: 60px;
    width: 370px;
    height: 60px;
    border: 0;
    font-size: 14px;
    line-height: 17px;
    font-family: 'Averta CY Bold';
    color: #FFFFFF;
    text-transform: uppercase;
    transition: all .3s;
    &:focus {
        outline: 0;
        font-size: 14px;
        line-height: 17px;
        font-family: 'Averta CY Bold';
        color: #FFFFFF;
        text-transform: uppercase; }
    &:hover {
        transition: all .3s;
        box-shadow: 0px 15px 25px rgba(250, 67, 67, 0.3);
        background: darken(#FA4343, 3%); } }

::placeholder {
    font-size: 18px;
    line-height: 22px;
    color: #131313; }

