.modal {
    background: #FFFFFF; }

.modal_close_btn {
    position: absolute;
    top: 35px;
    right: 35px;
    cursor: pointer;
    transition: all .3s;
    &:hover {
        transition: all .3s;
        transform: rotate(90deg); } }

//Обратная связь
#modal_feedback .iziModal-content {
    padding: 45px 65px 50px !important; }

.modal_feedback_header {
    font-size: 32px;
    line-height: 40px;
    font-family: 'Averta CY Bold';
    color: #131313;
    margin-bottom: 45px; }

.modal_form {
    input {
        width: 100%;
        margin-bottom: 20px; }

    .feedback_btn {
        width: 100%;
        margin-top: 10px; } }

//Благодарность
#modal_thank {
    text-align: center;
    p {
        text-align: left; } }

.modal_thank_img {
    margin-bottom: 30px; }

.modal_thank_header {
    font-family: 'Averta CY Bold';
    font-size: 30px;
    line-height: 37px;
    color: #131313;
    margin-bottom: 20px; }

.modal_thank_text {
    font-size: 18px;
    line-height: 22px;
    color: #2E2E2E;
    margin-bottom: 0; }

//Меню
#modal_menu .iziModal-content {
    padding: 55px 45px 55px 60px !important; }

#modal_menu {
    margin-left: 0;
    font-family: 'Averta CY Bold';
    border-top-left-radius: 0 !important;
    border-bottom-left-radius: 0 !important;
    .privacy {
        display: block;
        min-width: 100px;
        text-align: center;
        font-size: 14px;
        line-height: 17px;
        color: #C4C4C4;
        transition: all .3s;
        &:hover {
            transition: all .3s;
            color: darken(#878787, 20%); } }
    .contacts {
        display: none; }
    .messangers {
        display: flex;
        justify-content: space-around;
        align-items: center;
        max-width: 220px;
        margin: 0 auto; }
    .messanger {
        margin: 0;
        transform: scale(1.45); } }
.menu {
    margin-bottom: 40px;
    li {
        margin-bottom: 20px; }
    a {
        font-size: 18px;
        line-height: 22px;
        color: #000000;
        text-transform: uppercase;
        text-decoration: none;
        position: relative;
        &:before {
            content: '';
            height: 2px;
            background-color: #000000;
            width: 0;
            position: absolute;
            bottom: -2px;
            left: 0;
            transition: all .3s; }
        &:hover:before {
            transition: all .3s;
            width: 100%; } } }

#close_menu {
    top: 30px;
    right: 30px; }

//Политика
#privacy_modal .iziModal-content {
    padding: 50px 70px !important; }

.privacy_text {
    font-size: 12px;
    line-height: 15px;
    text-indent: 10px;
    color: #131313; }

#close_privacy_modal {
    top: 50px;
    right: 50px; }
